@import '_var';

main{
  .s_word{
    margin: $margin-m 0;
  }
  section.articles{
    display: flex;
    flex-wrap: wrap;
    @include sp(){
      display: block;
    }
    .article{
      margin-bottom: $margin-m;
      &:nth-child(2n-1){
        @include width_margin('L', $margin-s, 5, 5);
        @include sp(){
          @include clear_width_margin('L');
        }
      }
      &:nth-child(2n){
        @include width_margin('R', $margin-s, 5, 5);
        @include sp(){
          @include clear_width_margin('R');
        }
      }
      .article_head{
        display: flex;
        justify-content: space-between;
        padding: 3px;
        font-size: $font-s;
        @include background_purple();
      }
      .title{
        margin-top: $margin-s;
      }
    }
  }

  section.showMore{
    width: 100%;
    .showMore_btn{
      padding: 5px 10px;
      width: fit-content;
      margin: 0 auto;
      font-size: $font-s;
      @include background_black();
      @include hover_opacity();
    }
  }
}